<template>
	<view class="personInpt">
		<comp-input :xing="true" :firstName="start" :password='false' @myblur="myblurnation" placer="个人姓名"></comp-input>
		<comp-input :xing="true" :firstName="start" :password='false' @myblur="mybluridCard" placer="公民身份证号">
		</comp-input>
		<picker :value="classIndex" class="pickerMargin" :range="sexList" @change="classListPChange"
			:range-key="'dictName'">
			<view class="picker displayRow pickerAll">  
				<image :src="start" class="pickerstart"></image>
				<text  class="birthdayStyle" v-if="perData.userSex!= ''">{{perData.userSex}}</text>
				<text v-else>性别</text> 
				<!-- {{ sexList.dictName }} -->
			</view>
		</picker>
		<view @click="showTime" class="picker displayRow pickerAll">
			<image :src="start" class="pickerstart"></image>
			<text class="birthdayStyle" v-if="perData.birthdayStr">{{perData.birthdayStr}}</text>
			<text v-else>出生年月日</text>
		</view>
			<picker :value="index" class="pickerMargin" :range="nationList" @change="nationListChange"
			:range-key="'dictName'">
			<view class="picker displayRow pickerAll">  
				<image :src="start" class="pickerstart"></image> 
				<text  class="birthdayStyle" v-if="nationName">{{nationName}}</text>
				<text v-else>民族</text> 
			</view>
		</picker>






		<comp-input :xing="true" :firstName="start" :password='false' @myblur="mybluraddress" placer="住址">
		</comp-input>
		<comp-input :password='false' @myblur="mybluremail" placer="邮箱"></comp-input>
		<uni-popup ref="popupPicker" type="bottom">
			<picker-time @noPicker="closePicker" @okPicker="okPicker" :visible="visible"></picker-time>
		</uni-popup>


      <view class="loginButtom">
      	<button type="primary" @click="personalData" >登录</button>
      </view>
	</view>
</template>

<script>
	import {
		start
	} from "@/static/images/lxg/base64.js";
	// import { dictsearch } from "@/api/index.js";
	import compInput from "../compInput/index.vue"
	import pickerTime from "../pickerTime/index.vue"
	import pickers from "../picker/index.vue"
	import uniPopup from "../uni-popup/uni-popup.vue"
	 
	import { personal  } from "@/api/index.js";
	export default {
		components: {
			compInput,
			pickerTime,
			uniPopup,
			pickers
		},
		props: ["nationList", "userSexList"],
		watch: {
			userSexList(old) {

				old.forEach((ittems) => {
					this.sexList.push({
						'dictName': ittems.dictName,
						'dictCode': ittems.dictCode
					})
					console.log('======>', this.sexList)
				})
			}

		},
		data() {
			return {
				disabled:true,
				sexList: [],
				visible: false,
				start: start,
				perData: {
					nation: "",
					idCard: "51362419820705523x",
					address: "",
					email: "",
					userSex: "",
					birthdayStr: "",
					userName: "刘凤勇",
					userSexCode:""
				}, 
				index: null,
				ssss: false,
				classIndex: "",
				nationIndex: "",
				getdictName: '',
				nationName:""


			};
		},
		onShow(options) {

		},
		methods: {
			postPersonal(){
				let _this = this
				let getPer =this.perData
				let params={
					userName:getPer.userName,
					address: getPer.address,
					birthdayStr:  getPer.birthdayStr,
					email:  getPer.email,
					idCard:  getPer.idCard,
					nation:  getPer.nation, 
					userSex: getPer.userSexCode,
				}
				console.log("提交的参数",params)
				personal( params ).then(res => {
					console.log("ssssssss=====>",res)
					if(res.code==200){
						_this.$emit("okpersonaldata")
						 
						 
					}
					 
				
				})
			},
            personalData(){
			 
				let params = this.perData
				if(params.nation == ""){
					uni.showToast({
						icon:'none',
						title:"请选择名族"
					})
					return
				}
				if(params.idCard == ""){
					uni.showToast({
						icon:'none',
						title:"请输入生份证号码"
					})
					return
				}
				if(params.address == ""){
					uni.showToast({
						icon:'none',
						title:"请填写地址"
					})
					return
				}
				 
				if(params.userSex == ""){
					uni.showToast({
						icon:'none',
						title:"请选择性别"
					})
					return
				}
				if(params.birthdayStr == ""){
					uni.showToast({
						icon:'none',
						title:"请检查出生年月日"
					})
					return
				}
				if(params.userName == ""){
					uni.showToast({
						icon:'none',
						title:"请检查个人姓名"
					})
					return
				}
				 this.postPersonal()
				console.log("99999999999")
			},
			nationListChange(e){
				var thisIndex = e.detail.value;
				this.nationName = this.nationList[thisIndex].dictName;
				this.perData.nation = this.nationList[thisIndex].dictCode 
				this.nationIndex = thisIndex;
				 
			},
			classListPChange(e) {
				var thisIndex = e.detail.value;
				this.perData.userSex = this.sexList[thisIndex].dictName;
				this.perData.userSexCode = this.sexList[thisIndex].dictCode
				 
				this.classIndex = thisIndex;

				 
			},
			bindPickerChange: function(e) {
				console.log('picker发送选择改变，携带值为', e.target.value)
				// this.index = e.target.value
			},

			showTime() {
				this.visible = true
				this.getpopupPicker()
			},
			okPicker(v) {
				this.perData.birthdayStr = v
				this.visible = false
				this.getclosePicker()
			},
			closePicker() {

				this.getclosePicker()
			},
			getpopupPicker() {
				this.$refs.popupPicker.open()
			},
			getclosePicker() {
				this.$refs.popupPicker.close()
			},
			myblurnation(messarr) {
				this.perData.userName = messarr[0]

			},

			mybluridCard(messarr) {
				this.perData.idCard = messarr[0]

			},
			mybluraddress(messarr) {
				this.perData.address = messarr[0]

			},
			mybluremail(messarr) {
				this.perData.email = messarr[0]

			},
			noInputData(){
				
					let params = this.perData
					if(params.nation != ""&&params.idCard != ""&&params.address != ""&&params.email != ""
					&&params.userSex != ""&&params.birthdayStr != ""&&params.userName != ""
					){ 	this.disabled =false
					}
				 
			}
		},

	};
</script>

<style scoped>
	.personInpt {
		padding: 0px 40upx;
		padding-bottom: 200upx;
	}

	.displayRow {
		color: #8b949f;
		font-size: 14px;
	}

	.pickerMargin {
		margin: 40upx 0;
	}

	.displablock {
		display: block;
		font-size: 16px;
		line-height: 1.4em;
		height: 1.4em;
		min-height: 1.4em;
		overflow: hidden;
	}

	.pickerAll {
		height: 80upx;
		border: 1px solid #CCCCCC;
		border-radius: 10upx;
	}

	.pickerstart {
		width: 16upx;
		height: 16upx;
		margin-right: 20upx;
		margin-left: 20upx;
	}
	.loginButtom {
		margin-top: 40px;
		 
		
	}
	
	.loginButtom button {
		border-radius: 100upx;
		 
	}
	.birthdayStyle{
		color: #333;
	}
</style>
